// react hooks & libraries
import React, {useState, useCallback, useEffect} from 'react'

// css import
import styles from './RightFrame.module.css'
import color from '../../Res/ColorsString'
import CurieuxBlueLogo from '../../Res/SvgCompnents/CurieuxBlueLogo'
import ImageInputIcon from '../../Res/SvgCompnents/ImageInputIcon'
import ContentInputIcon from '../../Res/SvgCompnents/ContentInputIcon'
import ArrowDownSmall from '../../Res/SvgCompnents/ArrowDownSmall'

// components import
import Card from '../UI/Card'
import StreamDropdown from './Utils/StreamDropdown'
import StreamDropdownItem from './Utils/StreamDropdownItem'
import ImageInputOverlay from './Utils/ImageInputOverlay'
import ContentInputOverlay from './Utils/ContentInputOverlay'


const RightFrame = () => {

    // drop-down content selector -- in mind prop function data flow up -- data type suggestion id
    const [selectedStream, setSelectedStream] = useState("SELECT CATEGORY")
    const [dropdownState, setDropDownState] = useState(0)

    // id's 
    const STREAM_DROPDOWN_ID = "create-post-dropdown"
    const STREAM_DROPDOWN_DIALOG_ID = "drop-down-dialog"

    // dom elements
    const [dropdownDOM, setDropdownDOM] = useState(document.getElementById(STREAM_DROPDOWN_ID))
    const [dropdownDialogDOM, setDropdownDialogDOM] = useState(document.getElementById(STREAM_DROPDOWN_DIALOG_ID))
    const [arrowDownColor, setArrowDownColor] = useState(color.gray)
    
    
    useEffect(() => {
        setDropdownDOM(document.getElementById(STREAM_DROPDOWN_ID)) 
        setDropdownDialogDOM((document.getElementById(STREAM_DROPDOWN_DIALOG_ID)))
        setDropDownState(0)
    },[])

    

    // animations controlling
    // TODO: open drop-down 
    //      ----transform arrow button
    //      ----let the border color remain blue 
    //      ----the text color and arrow color black

    const streamDropdownOnClickHandler = useCallback((event) => {

        if (dropdownState === 0) {
            dropdownDOM.lastChild.style.transform = "rotate(-180deg)"
            dropdownDOM.style.borderColor = color.light_blue
            dropdownDOM.style.color = color.rich_black
            setArrowDownColor(color.rich_black)

            dropdownDialogDOM.style.display = "grid"       
            setDropDownState(1)
        } else {
            dropdownDOM.lastChild.style.transform = "rotate(0deg)"
            dropdownDOM.style.borderColor = color.gray
            dropdownDOM.style.color = color.gray
            setArrowDownColor(color.gray)

            dropdownDialogDOM.style.display = "none" 
            setDropDownState(0)
        }


    }, [dropdownDOM, dropdownDialogDOM, dropdownState])

    const streamDropdownOnMouseEnterHandler = useCallback((event) => {

        // dropdownDOM.lastChild.style.transform = "rotate(-180deg)"
        dropdownDOM.style.borderColor = color.light_blue
        dropdownDOM.style.color = color.rich_black
        setArrowDownColor(color.rich_black)
    }, [dropdownDOM])

    const streamDropdownOnMouseLeaveHandler = useCallback((event) => {

        if (dropdownState === 0) {
            dropdownDOM.lastChild.style.transform = "rotate(0deg)"
            dropdownDOM.style.borderColor = color.gray
            dropdownDOM.style.color = color.gray
            setArrowDownColor(color.gray)

            dropdownDialogDOM.style.display = "none"
        }
        
    }, [dropdownDOM, dropdownDialogDOM, dropdownState])
    





    // input overlay controls

    const [isActiveImageInputOverlay, setIsActiveImageInputOverlay] = useState(0)
    const [isActiveContentInputOverlay, setIsActiveContentInputOverlay] = useState(0)

    const ImageInputIconOnClickHandler = useCallback(() => {
        setIsActiveImageInputOverlay(1)
    })    

    const ContentInputIconOnClickHandler = useCallback(() => {
        setIsActiveContentInputOverlay(1)
    })    


    return <Card background={color.white} className={styles['main-holder']}>

        <div className={styles['info-card']}>

            {/* TODO: header */}
            <div className={styles['info-card-header']}>
                <CurieuxBlueLogo />
                <span className={styles['info-card-header-text']}>WE VALUE YOUR&nbsp;</span>
                <span className={styles['info-card-header-subtext']}>CONTENT</span>
            </div>
            

            {/* TODO: add image input */}
            <div className={styles['info-card-icon-input']}>
                <button
                    className={styles['info-card-icon-input-button']}
                    onClick={ImageInputIconOnClickHandler}
                >
                    <ImageInputIcon />
                </button>
                <button
                    className={styles['info-card-icon-input-button']}
                    onClick={ContentInputIconOnClickHandler}
                >
                    <ContentInputIcon />
                </button>
            </div>

            {/* TODO: add content input */}
            <div className={styles['info-card-stream-input']}>
        
                <div
                    id={STREAM_DROPDOWN_ID}
                    className={styles['info-card-stream-input-dropdown']}
                    onClick={streamDropdownOnClickHandler}
                    onMouseEnter={streamDropdownOnMouseEnterHandler}
                    onMouseLeave={streamDropdownOnMouseLeaveHandler}
                >
                    <span className={styles['info-card-stream-dropdown-name']}>{selectedStream}</span>
                    <span
                        className={styles['info-card-stream-dropdown-arrow']}
                    >
                        <ArrowDownSmall color={arrowDownColor}/>
                    </span>
                </div>

                <div
                    id={STREAM_DROPDOWN_DIALOG_ID}
                    className={styles['info-card-stream-dropdown-dialog']}
                >
                    <StreamDropdown>
                        <StreamDropdownItem name='API'/>
                        <StreamDropdownItem name='C++'/>
                        <StreamDropdownItem name='THE LONG TITLE'/>
                        <StreamDropdownItem name='JAVA'/>
                        <StreamDropdownItem name='IOS'/>
                        <StreamDropdownItem name='API' />
                        <StreamDropdownItem name='C++' />
                        <StreamDropdownItem name='THE LONG TITLE' />
                        <StreamDropdownItem name='JAVA' />
                        <StreamDropdownItem name='IOS' />
                        <StreamDropdownItem name='API' />
                        <StreamDropdownItem name='C++' />
                        <StreamDropdownItem name='THE LONG TITLE' />
                        <StreamDropdownItem name='JAVA' />
                        <StreamDropdownItem name='IOS' />
                        <StreamDropdownItem name='API' />
                        <StreamDropdownItem name='C++' />
                        <StreamDropdownItem name='THE LONG TITLE' />
                        <StreamDropdownItem name='JAVA' />
                        <StreamDropdownItem name='IOS' />
                        <StreamDropdownItem name='API' />
                        <StreamDropdownItem name='C++' />
                        <StreamDropdownItem name='THE LONG TITLE' />
                        <StreamDropdownItem name='JAVA' />
                        <StreamDropdownItem name='IOS' />
                    </StreamDropdown>

                </div>
                
            </div>


            {/* TODO: add preview and publish buttons */}

            <div className={styles['info-card-details-button']}>
                <button
                    className={styles['info-card-details-button-preview']}
                >
                    PREVIEW
                </button>
                <button
                    className={styles['info-card-details-button-publish']}
                >
                    PUBLISH
                </button>
            </div>

            {/* TODO: add input overlays */}

            <ImageInputOverlay isActive={isActiveImageInputOverlay} activeHandler={setIsActiveImageInputOverlay}/>
            <ContentInputOverlay isActive={isActiveContentInputOverlay} activeHandler={setIsActiveContentInputOverlay} />
            
        </div>

    </Card>
}

export default RightFrame
