// react hooks & libraries
import React, { useCallback, useState, Fragment } from 'react'

// css import
import styles from './ContentInputOverlay.module.css'
import color from '../../../Res/ColorsString'
import Clear from '../../../Res/SvgCompnents/Clear'
import Send from '../../../Res/SvgCompnents/Send'

// components import


const ContentInputOverlay = (props) => {

    const [mouseEnterPostButton, setMouseEnterPostButton] = useState(0)


    const MouseEnterPostButtonHandler = useCallback(() => {
        setMouseEnterPostButton(1)
    })

    const MouseLeavePostButtonHandler = useCallback(() => {
        setMouseEnterPostButton(0)
    })


    const ClearOnClickHandler = useCallback(() => {
        props.activeHandler(0)
    })


    return <Fragment>

        <div
            className={styles['overlay-backdrop']}
            style={{
                display: props.isActive === 1 ? 'flex' : 'none'
            }}
        >

        </div>



        <div
            className={styles.overlay}
            style={{
                display: props.isActive === 1 ? 'flex' : 'none'
            }}
        >


            <button
                className={styles['overlay-clear-button']}
                onClick={ClearOnClickHandler}
            >
                <Clear />
            </button>





            <div
                className={styles['overlay-content']}
            >
                <textarea
                    className={styles['overlay-content-input']}
                    type='text'
                    autoComplete="none"
                    required={true}
                    placeholder={'ADD CONTENT ....'}
                    
                />
            </div>

            

            <div
                className={styles['overlay-word-limit']}
            >
                500/500
            </div>

            <div
                className={styles['overlay-end-button']}
            >
                <button
                    className={styles['overlay-post-button']}
                    onMouseEnter={MouseEnterPostButtonHandler}
                    onMouseLeave={MouseLeavePostButtonHandler}
                >
                    <span className={styles['overlay-post-button-text']}>POST</span>
                    <span className={styles['overlay-post-button-img']}><Send color={mouseEnterPostButton === 0 ? color.rich_black : color.white}/></span>
                </button>
            </div>

        </div>



    </Fragment>

}

export default ContentInputOverlay
