/*
    @StreamItem

    @task : to create look and feel of each item

    @arguments

    props.name      :  name of the stream
    props.count     :  total posts in the stream
*/ 

// react hooks & libraries
import React from 'react'
import {Link} from 'react-router-dom'

// css import
import styles from './StreamItem.module.css'
import color from '../../../Res/ColorsString'



// components import
import Paths from '../../../Res/Paths'



const StreamItem = (props) => {

    return <li
        className={styles['stream-list-item-container']}
        style={{
            color: color.gray
        }}
    >
        
        <Link to={Paths.STREAM_PAGE} className={styles['stream-list-item']}>{props.name}</Link>
        <span className={styles['stream-list-item-count']}>{props.count}</span>
        
    </li>

}

export default StreamItem